* {
  box-sizing: border-box;
}

body {
  color: rgb(255, 255, 255);
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
}
.root {
  display: flex;
  flex-direction: column;
}
.movie-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.movie {
  margin: 1rem;
  width: 250px;
  background-color: rgb(51, 51, 86);
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}

.movie img {
  height: 300px;
  object-fit: cover;
  width: 100%;
}
.movie-info {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
}
.movie-info h3 {
  margin: 0;
}
.movie-over {
  background-color: white;
  color: rgb(4, 21, 78);
  font-size: 13px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  transform: translateY(100%);
  transition: transform 0.3s ease-in-out;
  overflow: auto;
  max-height: 100%;
  box-shadow: 0 0 7px 5px rgba(49, 128, 247, 0.942);
}
.movie:hover .movie-over {
  transform: translateY(0%);
}
header {
  padding: 1rem;
  display: flex;
}
.title {
  color: rgb(135, 35, 35);
  margin: auto;
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
  font-family: 'Black Ops One', sans-serif;
  
}

.search {
  font-size: 1.2rem;
  border-radius: 10px;
  border: 2px solid rgb(77, 98, 164);
  box-shadow: 0 3px 5px rgb(77, 98, 164);
  height: 40px;
  text-align: center;
  font-size: 0.8rem;
  padding: 0.5rem 1.5rem;
  transition: background-color 0.3s;
}

.search:hover {
  background-color: #c2daf1;
}
.search:focus {
  outline: 0;
}

.tag {
  background-color: rgb(215, 227, 249);
  padding: 0.5rem;
  border-radius: 5px;
  font-size: bold;
}

.tag.green {
  color: green;
}

.tag.blue {
  color: rgb(19, 64, 241);
}

.tag.red {
  color: red;
}

.footer {
  color: rgb(4, 21, 78);
  font-size: 13px;
  font-family: Helvetica;
  display: flex;
  text-align: center;
  justify-content: center;
  margin-top: 90px;
  margin-bottom: 30px;
}
